


<template>
  <div>
    <div class="mengban" v-show="control" @click="control = false"></div>
    <div class="bkground">
      <img style="width: 100%; height: 100%;" src="../common/images/land.png" />
    </div>
    <div class="land">
      <div style="padding-top: 5px;">
        <van-icon class="van-icon van-icon-arrow-left" @click="$router.go(-1)" />
      </div>
      <div class="cartIcon">
        <img
          style="width: 100%; height: 100%;"
          src="../common/images/logo@2x.png"
        />
      </div>
      <van-tabs v-model="active">
        <van-tab title="Sign In">
          <div class="fromWrap">
            <van-cell-group>
              <div class="userName"  :class="{ errorShow: hide }">
                <van-field
                  v-model="username"
                  AUTOCOMPLETE="off"
                  clearable
                  placeholder="Username or Email or Phone"
                />
              </div>
              <div class="passwd">
                <p class="errorTips" id="tisp">
                  <!-- <a class="iconfont"> &#xe657; </a>Wrong Email Address, Please
                  check out and refill. -->
                </p>
                <van-field
                  v-model="pwd"
                  AUTOCOMPLETE="off"
                  type="password"
                  placeholder="Password"
                ></van-field>
              </div>
            </van-cell-group>
            <span class="forgetPwd" @click="$router.push('resetPwd')"
              >Forget Password?</span
            >
            <van-button type="danger" @click="login">Sign In</van-button>
            <div class="or">OR</div>
            <div class="facebook" @click="facebookLogin">
              <span class="iconfont"> &#xe6d4; </span> Login with Facebook
            </div>
            <!-- <div class="twitter" @click="twitterLogin">
              <span class="iconfont"> &#xec9c; </span> Login with Twitter
            </div> -->
            <div class="privacy">
              By clicking the SIGN UP button, you confirm that you accept our
              <font>Terms of</font> uee and <font>Privacy Policv</font>
            </div>
          </div>
        </van-tab>
        <van-tab title="Sign Up">
          <div class="fromUpWrap">
            <div class="personal">
              <van-field
                style="width: 48%;margin-right: 4%;"
                v-model="nickname"
                placeholder="Name"
              />
              <div class="pwdWrap" @click="control = true">
                <van-field
                  style="border-bottom: 1px solid #999999;"
                  disabled
                  @click="showGender = true"
                  v-model="sex"
                  placeholder="Gender"
                  right-icon="arrow-down"
                />
                <div class="witch" v-show="control">
                  <!-- <p>Men</p>
                  <p>Women</p> -->
                  <p
                    v-for="(item, index) in genderList"
                    class="hover"
                    @click="genderSel(item.name)"
                    :key="index"
                  >
                    {{ item.name }}
                  </p>
                </div>
              </div>
            </div>
            <van-cell-group>
              <div class="telWrap" v-if="useTel">
                <span class="areaCode"
                  >Qatar +61 <a class="iconfont cart"> &#xe671; </a></span
                >
                <van-field type="tel" v-model="tel" />
              </div>
              <van-field
                type="email"
                v-model="email"
                v-if="!useTel"
                placeholder="Email"
              ></van-field>
            </van-cell-group>
            <van-button
              type="danger"
              @click="useTel = true"
              v-if="!useTel"
              class="switch"
              >Use your mobile number instead</van-button
            >
            <van-button
              type="danger"
              @click="useTel = false"
              v-if="useTel"
              class="switch"
              >Use your email instead</van-button
            >
            <van-cell-group>
              <van-field
                v-model="registerPwd"
                type="password"
                placeholder="Password"
              ></van-field>
              <div class="invitationCode">
                <van-field
                  v-model="invitationCode"
                  type="text"
                  placeholder="Invitation Code"
                  right-icon="question-o"
                  @click-right-icon="getCode = true"
                ></van-field>
              </div>
            </van-cell-group>
            <van-button type="danger" class="btm" @click="register"
              >Sign Up</van-button
            >
            <div class="privacy">
              By clicking the SIGN UP button, you confirm that you accept our
              <font>Terms of</font> uee and <font>Privacy Policv</font>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <div class="transparent" v-show="getCode"></div>
      <div class="getCode" v-show="getCode">
        <div class="photo">
          <div class="codeWrap" @click="$router.push('map')">
            Get Code
          </div>
          <div class="deso">
            <span class="iconfont back" @click="getCode = false">&#xe66c;</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { sendSms, sendEmail } from "@/api/msg";
import{signInAuth} from '@/api/user';
export default {
  name: "index",
  data() {
    return {
      username: "",
      pwd: "",
      tel: "",
      nickname: "",
      sex: "Man",
      active:0,
      email: "",
      invitationCode: this.$route.query.invitationCode,
      from:this.$route.query.from||'/',
      registerPwd: "",
      genders: ["Man", "Woman", "-"],
      control: false,
      useTel: true,
      hide: false,
      genderList: [{ name: "Men" }, { name: "Women" }],
      getCode: false
    };
  },
  created() {
    if(this.invitationCode){
      this.active=1;
    }
  },
  methods: {
    login() {
      console.log(1)
      var code='';
      if(this.username == ''){
        console.log(2)
        var oDiv=document.getElementById("tisp");
        oDiv.innerHTML='<a class="iconfont"> &#xe657; </a>Please input your user information';
        console.log(3)
        this.$toast('Please input your user information');
        return false;
      }else if(this.pwd == ''){
        var oDiv=document.getElementById("tisp");
        oDiv.innerHTML='<a class="iconfont"> &#xe657; </a>Please input your password';
        this.$toast('Please input your user information');
        return false;
      }
      this.$store
        .dispatch("login", { username: this.username, password: this.pwd })
        .then(r => {
          this.$toast(" Sign in successfully");
          console.log(567)
          if(this.from){
            this.$router.push(this.from);
          }
        }).catch(err => {
           
        });
    },
    register() {
      let param = {
        nickname: this.nickname,
        telephone: this.useTel ?  this.tel:null,
        email: this.useTel ? null : this.email,
        loginPassword: this.registerPwd,
        sex: this.genders.indexOf(this.sex) + 1,
        invitationCode: this.invitationCode
      };
      if(this.nickname == ''){
        this.$toast(" Nickname cannot be empty");
        return false;
      } 
      if(this.useTel == false){
        if(this.email == ''){
          this.$toast(" email cannot be empty");
          return false;
        }  
      } 
      if(this.useTel == true){
        if(this.tel == ''){
          this.$toast(" telephone cannot be empty");
          return false;
        }  
      } 
      console.log(this.invitationCode)
      if(this.registerPwd == ''){
        this.$toast(" loginPassword cannot be empty");
        return false;
      }  
      if(this.invitationCode == '' || this.invitationCode == undefined){
        this.$toast(" invitationCode cannot be empty");
        return false;
      }  
      let username= this.useTel ?  this.tel : this.email;
      let sendFunc = this.useTel ? sendSms : sendEmail;
      sendFunc(username, "register").then(res => {
        this.$toast("Code send successfully");
        this.$router.push({
          path:  "verifyCode",
          query: {
            type: "register",
            accountType:this.useTel?2:1,
            username:this.useTel?this.tel:this.email,
            extra: JSON.stringify(param)
          }
        });
      }).catch(err => {
            
        });
    },
    facebookLogin() {
        this.thirdLogin("0");
    },
    twitterLogin() {  
        this.thirdLogin("1");
    },
    thirdLogin(type){
      this.$store.dispatch("thirdLogin", { type: type }).then(res => {
        this.$toast(JSON.stringify(res));
        if(res.state==0){
            res.remark=JSON.stringify(res);
            signInAuth(res).then(res=>{
              this.$toast(" FaceBook Login successfully");
              this.$router.go(-1);
            });

        }
      });
    },
    genderSel(genderName) {
      this.sex = genderName;
      this.control = false;
      this.showGender = false;
    }
  },
};
</script>

<style>
  .land .van-tabs{
    margin-top: 15px;
  }
  .land .van-tabs__nav{
    background-color: unset;
    background-color: transparent;
  }
  .land .van-hairline--top-bottom::after{
    border-width:0 0 1px 0;
    border-color:rgba(255,255,255,.3)

  }
  .van-cell:not(:last-child)::after{border-color:rgba(255,255,255,.3)}
  .land .van-tab{
    color: white;
  }
  .land .van-tabs__line{
    width: 24px !important;
    left: 33px;
  }
  .land .van-tab span{ font-size: 15px;}
  .land .van-tab--active span{
    font-size: 17px;
  }
  .land .van-field__control{
    color: white;
  }
  .land .van-cell-group,.van-cell{
    background-color: unset;
    background-color: transparent;
    color:#fff;  
    FILTER: alpha(opacity=0);
    -moz-appearance:none;
  	-webkit-appearance:none;
  }
  .land .van-cell{
    padding: 15px 0;
  }
  .land .van-cell:not(:last-child)::after{
    left: 0;
  }
  .land .van-button{
    width: 100%;
    margin: 10px 0;
  }
  .mengban{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 111;
    background-color: rgba(0, 0, 0, .6)
  }
  /* .hover:hover{
    background-color:#e33946;
  } */
</style>

<style lang="scss" scoped>

.land {
  width: 100%;
  height: 100%;
  position: relative;
}
.bkground {
  background: url("../common/images/land.png") no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  width: 100vw;
  height: 100vh;
  position: fixed;
}
.van-icon {
  color: white;
  font-size: 19px;
  padding-left: 16px;
  padding-bottom: 5px;
}
.cartIcon {
  width: 69px;
  height: 69px;
  border-radius: 5px;
  margin: auto;
}
.signWrap {
  width: 100%;
  height: 36px;
  font-size: 15px;
  padding-top: 10px;
  color: white;
  display: -webkit-box;
}
.sign_in {
  width: 50%;
  display: block;
}
.sign_up {
  width: 50%;
  display: block;
}
.btm {
  border-bottom: 2px #e33946 solid;
}
.fromWrap {
  width: 86%;
  margin: 0 7% 0 7%;
}
.fromWrap p {
  color: #e33946;
}
.fromUpWrap {
  width: 86%;
  margin: 2% 7% 0 7%;
}
.forgetPwd {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: white;
  margin-top: 8px;
  margin-bottom: 60px;
}
.or {
  padding: 10px 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 17px;
}
.van-button__text {
  font-size: 15px;
}
.facebook {
  margin: 10px 0 20px 0;
  width: 100%;
  height: 45px;
  font-size: 15px;
  background: rgba(21, 76, 165, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
}
.facebook .iconfont,
.twitter .iconfont {
  position: absolute;
  left: 15px;
  top: 50%;
  margin-top: -14px;
