  justify-content: center;
  position: relative;
  color: white;
}
.privacy {
  font-family: San-Francisco-Text-Regular;
  font-size: 8px;
  color: white;
  padding-top: 5px;
}
.privacy font {
  color: #2b87d5;
}
.personal {
  width: 100%;
  display: -webkit-inline-box;
}
.switch {
  background: rgba(227, 57, 70, 0.5);
  border: none;
}
