  margin-top: 60px;
}
.pwdWrap {
  width: 48%;
  position: relative;
}
.pwdWrap .witch {
  position: absolute;
  right: 0;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px 2px rgba(23, 23, 23, 0.2);
  padding: 5px 5px 10px;
